@keyframes slide{
  0%{opacity:initial}
  50%{opacity:0}
  100%{opacity:initial}
}

.sliding{
  animation: slide 1s ease-in-out !important;
  opacity: 1;
}


.menu {
  font-size: 0.5rem;
  transition: 200ms ease;
  position: sticky;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 0px 0px 5px 5px;
  text-transform: uppercase;
  /*animation: fadein var(--fadetime) ease;*/
  width: 100%;
  align-self: center;
  color:white;
}


.brand {
  /*animation: fadein calc(var(--fadetime)+1s) ease;*/
  animation-delay: 1s;
    /*color: var(--secondary);*/
  border-radius: 0.5rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
  margin-left: auto;
  margin-right: auto;
  text-align: center;

  
}
.brand-title {
  color:inherit;
  border-radius: 0.7rem;
  font-size: 2.5rem;
  /*text-shadow: 3px 1px 2px var(--colour8);*/
  font-weight: bold;
  /*background-color: rgba(29,45,68,0.5);
  padding: 0.5rem 1rem;  */
}
.brand-subtitle {
  user-select: none;
  font-size: 1rem;
  margin:0;
  padding:0;
  font-weight: normal;
}

/*
.brand-subtitle .s1{
  color:var(--sixth);
}
.brand-subtitle .s2{
  color:var(--seventh);
}
*/

.menu-list {
  /*color: var(--backgroundcolor);
  color: rgb(233, 233, 233);*/
  
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 1rem;
  /*border-bottom:  var(--fontcolor) solid 0.2rem;*/
  user-select: none;
  /*box-shadow: 10px 10px 15px -3px rgba(222,222,222,1);*/
  
  /*animation: fadein var(--fadetime) ease;*/
  padding-top: 0.8rem;
  color:white;
}

.menu-item {
  transition: 500ms ease;
  flex: 1;
  color: var(--secondary);
  color:transparent;

  text-decoration: none;
  user-select: none;
  cursor: pointer;
  filter: grayscale(100%);

  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  --iconsize:3.5rem;
  min-width: var(--iconsize);
  min-height: var(--iconsize);
  max-width: var(--iconsize);
  max-height: var(--iconsize);
  margin:calc(var(--iconsize)/3);
}

.menu-item .image {
  width: 100%;
  height: 100%;
}

.menu-item:hover {
  filter: hue-rotate(30deg);
  transform: scale(1.1);
  display: flex !important;
  color:rgba(255, 255, 255, 0.4) !important;
}


.menu-item.active {
  font-weight: bold;
  filter: none;
}


.menu-item .title{
  color: inherit;
  font-size: 0.8rem;
  text-align: center;
  margin-top:0.8rem;
}
.menu-item.active .title{
  color:white !important;
}
/*
.menu-item.active .title{
  opacity: 1 !important;
}*/

.socials-menu{
  animation:fadein 2s ease-in-out;
  transition: 300ms ease-in-out;

  flex-direction: row;
  bottom:0;
  right:0;
  left:0;
  
  align-self: center;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 4;
  filter:grayscale(0);
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}


/* for small screens*/
@media only screen and (max-width: 600px) {
  
  .menu-list {
    border-bottom: none;
    order: 2;
  }

  .menu {
    flex-direction: column;
    width: 100%;
    margin-bottom:1rem;
  }

  .brand {
    order: 1;
  }
}

